      }
      .relevant-card,
      .news-card{
        display: block;
        position: relative;
        .avatar{
          width: 2.5em;
          height: 2.5em;
          margin: 0 0 .5em;
          &:before{
            border-width: 2px;
          }
        }
        .text20,
        .text16{
          font-size: .5em;
        }
        .text14{
          font-size: .625em;
        }
        &__nav{
          position: absolute;
          top: 1em;
          left: 1.69em;
          z-index: 2;
        }
      }
    }
  }

  .user-main{
    position: static;
    width: 16.25em;
    max-width: 100%;
    transform: unset;
    margin: 0 auto;
  }

  .scroll-btn{
    display: none;
  }

  .wallet-link{
    width: calc(100% - 2em);
    height: 3.5em;
    border-radius: 1.5em;
    position: absolute;
    left: 1em;
    top: 100%;
    margin: 2em 0 0;
  }

  .balance{
    display: none;
  }

