    }
    &__badges{
      margin-bottom: .5em;
    }
    .badge{
      height: 1.375em;
      padding: 0 .5em;
      font-weight: 500;
      @include lastM(0 .25em 0 0);
      span{
        font-size: .625em;
      }
      svg{
        width: .625em;
        &:not(:last-child){
          margin-right: .125em;
        }
      }
    }
    .text40{
      font-size: 1em;
    }
    &__socials{
      margin-top: .5em;
    }
    &__social{
      @include lastM(0 1em 0 0);
      &-btn{
        background: $purple2;
        border-color: $purple2;
        width: 1.25em;
        height: 1.25em;
        margin-right: .25em;
        svg{
          width: .625em;
        }
        path{
          fill: #fff;
        }
      }
      .text16{
        font-size: .625em;
      }
    }
    &__text{
      max-width: 12.56em;
      .text16{
        font-size: .625em;
        @include truncate(4);
      }
    }
    &__btn{
      width: 2.5em;
      height: 2.5em;
      bottom: .25em;
      right: .13em;
      svg{
        width: 1.625em;
      }
    }
    .service-question{
      display: flex;
      max-width: 11.5em;
      padding: .375em .75em;
      background-color: $primary;
      margin: .5em 0 0;
      .text16{
        font-size: .625em;
        line-height: 100%;
        color: #fff;
      }
      &__btn{
        padding: 0 .5em;
        height: 1.3125em;
        border-color: #fff;
        color: #fff;
        margin-left: .13em;
        span{
          font-size: .625em;
        }
      }
    }
  }

  .mob-balance{
    display: block;
    padding-top: 1em;
    &__items{
      margin-top: .5em;
      @include grid100gap(1em);
    }
    .balance{
      &__nums{
        padding: 0;
        border: none;
      }
      &__num{
        white-space: nowrap;
        .text14{
          font-size: 1.9em;
        }
      }
      &__changes{
        margin-left: .125em;
        svg{
          width: .75em;
          margin: 0;
        }
        &._success{
          color: $green;
        }
      }
    }
    &__btn{
      margin-top: .25em;
      padding: .25em .75em;
      height: unset;
      font-weight: 400;
      span{
        line-height: 100%;
      }
    }
  }
}
