.info-block{
  padding: 4.35em 0 .75em;
  &__inner{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
}

.info{
  &__inner{
    clip-path: polygon( 0% 5.755%,0% 5.755%,0.058% 4.822%,0.225% 3.936%,0.492% 3.11%,0.851% 2.356%,1.292% 1.686%,1.806% 1.11%,2.384% 0.642%,3.017% 0.293%,3.696% 0.075%,4.412% 0%,76.63% 0%,76.63% 0%,77.555% 0.128%,78.417% 0.493%,79.209% 1.067%,79.921% 1.822%,80.545% 2.729%,81.073% 3.761%,81.497% 4.889%,81.808% 6.085%,81.998% 7.32%,82.058% 8.568%,82.058% 8.568%,82.07% 9.521%,82.118% 10.492%,82.209% 11.478%,82.345% 12.473%,82.532% 13.471%,82.773% 14.469%,83.074% 15.46%,83.438% 16.44%,83.87% 17.403%,84.375% 18.345%,84.375% 18.345%,85.122% 19.467%,85.935% 20.407%,86.805% 21.179%,87.721% 21.796%,88.673% 22.27%,89.648% 22.614%,90.638% 22.843%,91.631% 22.968%,92.616% 23.003%,93.584% 22.96%,93.584% 22.96%,94.526% 22.977%,95.456% 23.171%,96.353% 23.532%,97.197% 24.046%,97.968% 24.703%,98.645% 25.49%,99.207% 26.396%,99.634% 27.409%,99.905% 28.518%,100% 29.71%,100% 94.245%,100% 94.245%,99.942% 95.178%,99.775% 96.064%,99.508% 96.89%,99.149% 97.644%,98.708% 98.314%,98.194% 98.89%,97.616% 99.358%,96.983% 99.707%,96.304% 99.925%,95.588% 100%,4.412% 100%,4.412% 100%,3.696% 99.925%,3.017% 99.707%,2.384% 99.358%,1.806% 98.89%,1.292% 98.314%,0.851% 97.644%,0.492% 96.89%,0.225% 96.064%,0.058% 95.178%,0% 94.245%,0% 5.755% );
  }
  &__top{
    display: flex;
    align-items: center;
    flex-wrap: wrap;
  }
  .chat-item__count{
    margin-left: 1em;
  }
  &__btn{
    top: 0;
    right: 0;
  }
  .badge{
    height: 2.125em;
    padding: 0 .75em;
    svg{
      margin: 0 .5em 0 0;
      width: 1.125em;
    }
  }
  &__desc{
    width: 100%;
    margin-top: 1em;
  }
}

.rewards{
  display: flex;
  margin-top: 2.625em;
}

.reward{
  width: 11.86em;
  margin-right: 1em;
  padding: 1.32em 1.32em 1.97em;
  border-radius: 1.5em;
  background: #F6F6F6;
  backdrop-filter: blur(10.000000953674316px);
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__info{
    margin-top: 1.32em;
    @include center;
  }
  &__title{
    font-size: 1.32em;
    font-weight: 700;
  }
  &__changes{
    display: flex;
    align-items: center;
    margin-left: .26em;
    color: #009F85;
    font-weight: 500;
    svg{
      width: .625em;
      height: auto;
      margin-right: .13em;
    }
  }
}


@media screen and (max-width: 576px){
  .info-block{
    padding: .625em 0 1em;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .info{
    &__inner{
      min-height: unset;
      clip-path: unset;
      border-radius: 1.5em;
      padding: 1.5em;
    }
    &__top{
      padding-bottom: .875em;
      border-bottom: 1px solid $purple2;
    }
    .chat-item__count{
      min-width: 1em;
      height: 1em;
      margin: -.25em 0 0 .2em;
    }
    &__btn{
      width: 1.5em;
      height: 1.5em;
      top: 1.5em;
      right: 1.5em;
      svg{
        width: .75em;
      }
    }
    .news__items{
      margin-top: 1em;
    }
    &__desc{
      margin-top: .12em;
    }
  }

  .news-card2{
    align-items: flex-start;
    .avatar{
      width: 3.5em;
      height: 3.5em;
      margin-right: 1em;
    }
    .news-card__content{
      gap: .25em;
    }
    .text16{
      font-size: .875em;
      line-height: 115%;
    }
    .text14{
      font-size: .625em;
      line-height: 120%;
    }
    .news-card__nav{
      flex-direction: column;
      align-items: flex-start;
      margin-top: -.5em;
    }
    .badge{
      margin-top: .5em;
      height: 1.4375em;
      padding: 0 .5em;
      svg{
        width: .75em;
      }
    }
  }

  .rewards{
    margin-top: 1em;
    width: calc(100% + 1em);
  }

  .reward{
    width: calc(50% - 1em);
    padding: 1.25em 1.25em 1em;
    background: rgba(255, 255, 255, 0.40);
    &__img{
      width: 5.5em;
      max-width: 100%;
      margin: 0 auto;
    }
    &__info{
      margin-top: .75em;
    }
    &__title{
      font-size: 1em;
    }
    &__changes{
      svg{
        width: .53em;
      }
      span{
        font-size: .53em;
      }
    }
  }
}
